<template>

  <div id="HomePage">
<!--    //自我介绍-->
    <article class="hello">
      <h2>Hello!</h2>

      <p class="profiles">⭐欢迎来到我的博客，我是5t5，一个刚刚毕业的前端应届小白。</p>
      <p class="profiles">⭐立志学习更多的前端知识，常常逛掘金社区，还有GitHub。</p>
      <p class="profiles">⭐目前的技术栈是Html，Css3，Javascript，Node.js，Vue.js。</p>
      <p class="profiles">⭐我在这里将会发布一些有关于代码的文章，顺便记录一下自己的成长。</p>
      <p class="profiles">⭐如果你想要联系我，就请在下面contact me里提交信息吧</p>
      <p class="profiles">⭐本博客目前的功能还在搭建之中，后续将会完善......</p>
    </article>

    <!--    //时间轴-->
    <article class="time">
      <h2>Time Axis</h2>
      <div v-if="openBoolean" class="open" @click="openHei($event)">open▼</div>
      <div v-else class="open" @click="closeHei($event)">open▲</div>
      <article class="showT">
        <el-timeline>
          <el-timeline-item timestamp="2021/01--2021/12" placement="top" color="green">
            <el-card>
              <h3>07月毕业</h3>
              <p style="margin-bottom: 10px">正式结束三年的大学生活</p>
              <h3>01月-07月实习</h3>
              <p>于福州好好学习教育有限公司实习前端</p>
            </el-card>
          </el-timeline-item>
          <el-timeline-item timestamp="2020/01--2020/12" placement="top">
            <el-card>
              <h3>个人荣誉</h3>
              <p>院优秀共青团员；院文娱优秀生；院综合积分三等奖；国家励志奖学金。</p>
            </el-card>
          </el-timeline-item>
          <el-timeline-item timestamp="2019/01--2019/12" placement="top">
            <el-card>
              <h3>个人荣誉</h3>
              <p>英语B级证书；计算机一级证书；校庆海报设计三等奖；办公软件大赛二等奖；系优秀助导；全国哇哈哈营销大赛三等奖；院综合积分三等奖；院品德优秀生。</p>
            </el-card>
          </el-timeline-item>
          <el-timeline-item timestamp="2018/09" placement="top">
            <el-card>
              <h3>入学</h3>
              <p>就读于福建水利电力职业技术学院信息工程系软件技术专业。</p>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </article>
    </article>

    <!--    //热门文章-->
    <article class="article">
      <h2>Hot Article</h2>
      <p class="des">热门文章推荐 <a href="/article">更多文章</a> 请访问文章页（￣︶￣）↗</p>

<!--      文章内容-->
      <div class="fadeIn" v-for="item in hotArticle" :key="item._id">
        <div class="one">
          <div class="oneImg" :style="{backgroundImage: `url(${baseURL}${item.cover})`}"></div>
          <div class="inside">
            <p class="tit"><span>{{ item.title }}</span>  <span>{{ new Date(item.date).getFullYear() }}-{{ new Date(item.date).getMonth() + 1 }}-{{ new Date(item.date).getDate()  }}</span> </p>
            <p class="insideContent">{{item.des}}</p>

            <div class="two">
              <span><i class="iconfont icon-redu"></i>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ item.pv }}</span>
              <span><i class="iconfont icon-comment"></i></span>
              <router-link class="router" :to="`/article/${item._id}`">阅读全文</router-link>
            </div>
          </div>
        </div>
      </div>
    </article>

    <!--    //联系我-->
    <article id="contact">
      <h2>Contact Me</h2>
      <div style="margin: 20px;"></div>
      <el-form
          id="contactEl"
          status-icon
          :rules="rules"
          :model="contactForm"
          label-position="right"
          label-width="120px"
          ref="contactForm"
      >
        <el-form-item label="你的名字"  prop="name" label-width="120px">
          <el-input v-model="contactForm.name" label-width="120px" autocomplete="off" style="width:40%;" ></el-input>
        </el-form-item>
        <el-form-item label="电子邮箱" prop="email" label-width="120px">
          <el-input v-model="contactForm.email" label-width="120px" autocomplete="off" style="width:40%" ></el-input>
        </el-form-item>
        <el-form-item label="内容标题" label-width="120px" prop="subject">
          <el-input v-model="contactForm.subject" label-width="120px" style="width:40%" ></el-input>
        </el-form-item>
        <el-form-item label="主要内容" prop="message" label-width="120px">
          <el-input type="textarea" :rows="8" v-model="contactForm.message" autocomplete="off" label-width="120px" style="width:40%;" ></el-input>
        </el-form-item>
        <el-form-item label-width="120px">
          <el-button type="primary" @click="submit" >Send</el-button>
        </el-form-item>
      </el-form>
    </article>

  </div>

</template>

<script>

export default {
  name: "HomePage",
  data() {
    return {
      openBoolean:true,
      //表单数据
      contactForm: {
        name: "",
        email: "",
        subject: "",
        message: ""
      },
      //表单规则
      rules: {
        name: [
          {required: true, trigger: "blur"}
        ],
        email: [
          {type: "email", required: true, trigger: "blur"}
        ],
        message: [
          {required: true, trigger: "blur"}
        ],
        subject: [{type: "string"}]
      },
      //文章列表
      hotArticle: [],
    }
  },

  methods: {
    //Contact me的提交
    submit() {
      //需要验证表单的数据合不合要求
      //需要调用el-form组件的validate
      this.$refs.contactForm.validate(async valid=>{
        if (valid){
          let {data} = await this.$axios({
            method: "POST",
            url: "/contact",
            data: this.contactForm
          })

          if (data.code){
            return this.$message.error(data.message)
          }

          this.$message.success("发送成功，我会尽快回复的")
          this.$refs.contactForm.resetFields()
        }else{
          return false
        }
      })
    },
    //请求文章数据
    async getArticle() {
      let {data} = await this.$axios({
        method: "get",
        url: "/get/article"
      })
      if (data.code) return
      this.hotArticle = data.data.slice(0, 4)
    },
    //展开open
    openHei(e){
      e.currentTarget.parentElement.style.height = "730px";
      this.openBoolean = false
    },
    closeHei(e){
      e.currentTarget.parentElement.style.height = "50px";
      this.openBoolean = true
    }
  },


created() {
  this.getArticle()
}


}
</script>


<style scoped lang="less">

  #HomePage{

    article{
      min-width: 670px;
      background-color: rgba(255, 255, 255, .8);
      padding: 15px;
      margin-bottom: 15px;
      box-shadow: 0 0 4px #ddd;

      h2{
        margin-bottom: 15px;
        font-size: 30px;
        font-family: Quicksand;
        letter-spacing: 3px;
        padding-top: 10px;
      }

      p.des {
        padding-bottom: 20px;
        text-indent: 2em;
        color: #444;
        font-size: 16px;
        letter-spacing: 4px;
        line-height: 30px;

        a {
          text-decoration: underline;
          color: #aaa;
        }
      }

      }

      //个人介绍
    article.hello{
        p.profiles{
          font-family: Regular;
          margin-bottom: 10px;
          text-indent: 2em;
          color: #444;
          font-size: 22px;
          letter-spacing: 4px;
          line-height: 35px;
        }
      }

      //时间轴
    article.time{
      overflow: hidden;
      height:50px;
      transition: all .8s;

      h2{
        display: inline-block;
      }
      .open{
        font-family:Regular;
        font-size:20px;
        float:right;
        transform: translate(-20px,15px);
        cursor: pointer;
      }

      .showT{
        margin-top: 10px;

        /deep/ .el-timeline-item__timestamp.is-top{
          font-size: 14px;
        }

        p{
          display: inline-block;
          margin-top: 5px;
          font-size: 15px;
        }
      }

    }

      //文章
    article.article {

        .fadeIn {
          box-sizing: border-box;
          position: relative;
          box-shadow: 0 0 4px #ddd;
          width: 100%;
          margin-bottom: 15px;
          padding: 25px 20px;
          background-color: rgba(255, 255, 255, .7);

          &:hover{
            transform:translateY(-5px);
            transition:all .3s;
            box-shadow: 5px 5px 15px #ddd;
          }


          .one {
            display: flex;

            .oneImg {
              height: 210px;
              width: 315px;
              box-sizing: border-box;
              padding-right: 1%;
              background-size: cover;
              background-position: 50%;
              border-radius: 30px;
            }

            .inside{
              flex: 1;
              margin-left: 35px;

              p.tit{

                :nth-child(1){
                  font-size: 27px;
                  font-weight: 700;
                }

                :nth-child(2){
                  font-size: 22px;
                  float: right;
                  margin-right: 15px;
                }
              }

              p.insideContent{
                margin-top: 5px;
                display: inline-block;
                height: 130px;
                font-size: 16px;
                text-indent: 2em;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                white-space: pre-wrap;
                -webkit-line-clamp: 7;
                -webkit-box-orient: vertical;
              }

              .two{
                display: flex;
                text-align: center;
                align-items: center;

                span{
                  flex: 1;

                  i{
                    font-size: 23px;
                  }

                }

                .router{
                  flex: 1.5;
                  padding: 10px 20px;
                  background-color: #409eff;
                  color: #fff;
                  transition: opacity .3s;
                  vertical-align: 0px;

                  &:hover {
                    opacity: .8;
                  }
                }
              }
            }
          }
        }
      }

      //联系我
    article.contact {
      .el-form {
        padding: 0 20px;

      }

      .el-button{
        display: block;
        width: 100px;
        height: 40px;
        margin-top: 20px;
        border: 0;
        border-radius: 20px;
        box-shadow: 0 0 0 #bbb;
        background-color: #52b7ee;
        font-weight: 700;
        font-size: 16px;
        letter-spacing: 2px;
        font-family: Quicksand,"sans-serif";
        color: #000;
        outline: 0;
        cursor: pointer;
        transition: box-shadow .3s;
        &:hover{
          box-shadow: 0 3px 5px #bbb;
        }
      }
    }

    }


</style>