<!--左侧-->
    <div class="left">
      <InformationLeft></InformationLeft>
    </div>
<!--右侧-->
    <div class="right">
      <router-view/>
    </div>

    <el-backtop>
      <div
          style="{
        height: 100%;
        width: 100%;
        background-color: #f2f5f6;
        border-radius: 10px;
        box-shadow: 0 0 6px rgba(0,0,0, .12);
        text-align: center;
