        color: #1989fa;
      }"
      >
        UP
      </div>
    </el-backtop>
  </div>

<!--  备案-->
  <footer class="footer"><div @click="Go('https://beian.miit.gov.cn')">闽ICP备2021013407号</div></footer>

</template>

<script>
import AppHead from "./components/Head/AppHead";
import InformationLeft from "./components/InformationLeft/InformationLeft";
import {mapMutations} from "vuex";

  export default {
    name: 'App',
    components: {
      AppHead, InformationLeft
    },
    watch: {
      '$route': function (to, from) {
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
      }
    },
    methods: {
      ...mapMutations(['loginSuccess']),
      Go(url){
        window.location.href=url
      }
    },
    async created() {

      let {data} = await this.$axios({
        method: "POST",
        url: "/login/check",
      })

      //未登录
      if (data.code) {
        return
      }

      //已登录
      this.loginSuccess(data.data)

    },
  }

</script>

