<template>

  <div class="UserBox">
    <el-popover
        placement="bottom-end"
        trigger="hover"
        :width="175"
    >
      <template #reference>
        <div
            class="img"
            :style="{backgroundImage:`url(${baseURL}${userInfo.photo}${random})`}"
            :title="userInfo.username"
        ></div>
      </template>
      <div class="show">
        <el-button type="primary" size="mini" @click="info">修改信息</el-button>
        <el-button type="danger" size="mini" @click="logout">退出登录</el-button>
      </div>
    </el-popover>
  </div>

</template>

<script>
import {mapState,mapMutations} from "vuex";

export default {
  name: "Users",
  data(){
    return{
      random: `?_=${Date.now()}`
    }
  },
  components:{

  },
  watch:{
    userInfo() {
      this.random = `?_=${Date.now()}`
    }
  },
  computed:{
    ...mapState(['userInfo']),
  },
  methods:{
    ...mapMutations(['loginOut']),

    //修改信息
    info(){
      this.$router.push("/change")
    },

    //退出登录
    async logout(){
      let {data} =await this.$axios({
        method:"POST",
        url:"/login/out"
      })

      if (data.code){return}

      this.$message.success("已退出登录")
      this.loginOut()
      await this.$router.replace("/")

    },

  }
}
</script>

<style scoped lang="less">

.UserBox {
  width: 34px;
  height: 34px;
  padding: 8px 0;

  .img {
    width: 34px;
    height: 34px;
    background: none center center/cover;
    border-radius: 50%;
    cursor: pointer;
  }
}

</style>