<template>

  <div class="HotArticle">
    <h4>热门文章</h4>
    <ul>
      <li v-for="(item,index) in hotArticle" :key="item._id"><i>{{ index + 1 }}</i><router-link :to="`/article/${item._id}`" target="_blank">{{ item.title }}</router-link></li>
    </ul>
  </div>

</template>

<script>
export default {
  name: "HotArticle",
  data() {
    return {
      hotArticle: []
    }
  },
  async created() {
    let {data} = await this.$axios({
      method: "get",
      url: "/get/article"
    })
    if (data.code) return

    this.hotArticle = data.data.slice(0, 4)
  }
}
</script>

<style scoped lang="less">

.HotArticle{
  box-sizing: border-box;
  width: 100%;
  //font-family: Quicksand;
  box-shadow: 0 0 4px #ddd;
  background-color: rgba(255, 255, 255, .7);
  padding: 20px;

  h4{
    line-height: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e8e9e7;
    color: #383937;
    font-size: 16px;
  }

  ul{
    margin-top: 12px;
    li{
      box-sizing: border-box;
      overflow: hidden;
      display: flex;
      height: 40px;
      line-height: 40px;
      padding: 0 15px;
      &:nth-child(1) i{
        background-color: rgb(167,113,201);
      }
      &:nth-child(2) i{
        background-color: rgb(189,129,201);
      }
      &:nth-child(3) i{
        background-color: rgb(205,147,197);
      }
      &:nth-child(4) i{
        background-color: rgb(224,165,191);
      }

      i{
        display: block;
        width: 22px;
        height: 22px;
        margin-top: 9px;
        margin-right: 15px;
        border-radius: 50%;
        line-height: 16px;
        font-size: 16px;
        font-style: normal;
        color: #fff;
        text-align: center;
        font-weight: 100;
        font-family: Pacifico;
      }
      a{
        display: block;
        flex: 1;
        color: #787977;
        font-size: 14px;
        text-decoration: none;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
      }
    }
  }
}

</style>