import { createRouter, createWebHistory } from 'vue-router'
import HomePage from "@/views/HomePage";

const routes = [
  {
    path: "/",
    name: "HomePage",
    component: HomePage
  },
  {
    path: "/article",
    name: "Article",
    component: () => import('@/views/Article')
  },
  {
    path: "/leavingmessage",
    name: "LeavingMessage",
    component: () => import('@/views/LeavingMessage')
  },
  {
    path: "/friendchain",
    name: "FriendChain",
    component: () => import(/* webpackChunkName: "home" */'@/views/FriendChain')
  },
  {
    path: "/about",
    name: "About",
    component: () => import(/* webpackChunkName: "home" */'@/views/About')
  },
  {
    path: "/change",
    name: "Change",
    component: () => import(/* webpackChunkName: "home" */'@/views/ChangeUserMessage')
  },
  {
    path: "/admin",
    name: "Admin",
    component: () => import(/* webpackChunkName: "home" */'@/views/Admin')
  },
  {
    path: "/article/:id",
    name: "ArticleID",
    component: () => import(/* webpackChunkName: "home" */'@/views/ArticleID')
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from,savedPosition) {
    return {
      x: 0,
      y: 0
    }
  }

})

export default router
