<template>

  <div id="InformationLeft">

    <!--  个人信息-->
    <Personal></Personal>

    <div
        class="sticky"
        :class="{'fixed':ifFixed}"
    >
      <!--  热门文章-->
      <HotArticle></HotArticle>

      <!--  最近访客-->
      <Visitor></Visitor>

    </div>

  </div>

</template>

<script>
import Personal from "./InformationSide/Personal";
import HotArticle from "./InformationSide/HotArticle";
import Visitor from "./InformationSide/Visitor";

export default {
  name: "InformationLeft",
  data() {
    return {
      ifFixed: false
    }
  },
  components: {Visitor, HotArticle, Personal},
  methods: {
    scrollFn(){
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      this.ifFixed = scrollTop >= 235
    }
  },

  mounted() {
    this.scrollFn()
    window.addEventListener("scroll", ()=>{
      this.scrollFn()
    })
  }
}
</script>

<style scoped lang="less">

.sticky.fixed {
  position: fixed;
  top: 65px;
  width: 270px;
}

</style>