<template>
  <div class="login">

    <el-button type="info" plain @click="openLoginBox()">登录 / 注册</el-button>
    <!--    登录背景-->
    <div class="box" @click="closeLoginBox()" v-if="ifShow">
<!--      主要界面-->
      <div class="main" @click.stop>
        <el-tabs
            v-model="activeName"
        >
          <el-tab-pane label="立即登陆" name="first">
            <el-form
                :model="LoginForm"
                status-icon
                :rules="rules"
                ref="LoginForm"
                label-width="80px"
                @keydown.enter="LoginSubmit"
            >
              <el-form-item label="用户名" prop="user" label-width="80px">
                <el-input v-model="LoginForm.username"/>
              </el-form-item>
              <el-form-item label="密码" prop="pass" label-width="80px">
                <el-input type="password" v-model="LoginForm.password"/>
              </el-form-item>
              <el-form-item label-width="80px">
                <el-button type="primary" @click="LoginSubmit">立即登录</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="注册账号" name="second">
            <el-form
                :model="RegForm"
                status-icon
                :rules="rules"
                ref="RegForm"
                label-width="80px"
                @keydown.enter="RegSubmit"
            >
              <el-form-item label="用户名" prop="user" label-width="80px">
                <el-input v-model="RegForm.username"/>
              </el-form-item>
              <el-form-item label="密码" prop="pass" label-width="80px">
                <el-input type="password" v-model="RegForm.password"/>
              </el-form-item>
              <el-form-item label-width="80px">
                <el-button type="primary" @click="RegSubmit">立即注册</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>

      </div>


    </div>

  </div>


</template>

<script>
import {mapMutations} from "vuex"

export default {
  name: "Login",
  data(){
    return{
      ifShow:false,
      activeName:"first",

      //登录表单数据
      LoginForm: {
        username: "",
        password: ""
      },
      //注册表单数据
      RegForm: {
        username: "",
        password: "",
      },
      //表单字段规则
      rules: {
        username: [
          {required: true, message: "请输入用户名"},
          {
            validator(rule, value, cb) {
              //长度验证
              if (value.length < 2 || value.length > 8) {
                cb(new Error("请输入2~8位的用户名"))
              }
              //规则验证
              if (!/^[\w\u4e00-\u9fa5\u0800-\u4e00\uac00-\ud7ff]+$/.test(value)) {
                cb(new Error("只允许 数字 字母 _ 中/日/韩文"))
              }

              cb()
            }
          }
        ],
        password: [
          {required: true, message: "请输入密码"},
          {
            validator(rule, value, cb) {
              //长度验证
              if (value.length < 6 || value.length > 18) {
                cb(new Error("请输入6~18位的密码"))
              }
              //规则验证
              if (!/^[\w\[\]\/\\~`|<>,.?;':"{}!@#$%^&*()_+=-]+$/.test(value)) {
                cb(new Error("不允许的密码字符"))
              }

              cb()
            }
          }
        ],
      }
    }
  },
  methods:{
    //打开登录/注册弹窗
    openLoginBox() {
      this.ifShow = true
    },
    //关闭登录/注册弹窗
    closeLoginBox(){
      this.ifShow = false
    },

    //注册按钮
    RegSubmit(){
      this.$refs.RegForm.validate(async value => {

        if (value){
          //发送请求
          let {data} = await this.$axios({
            method:"POST",
            url:"/register",
            data:{
              username:this.RegForm.username,
              password:this.RegForm.password
            }
          })

          //code为真注册失败
          if(data.code){
            this.$message.error(data.message)
            return
          }

        // 注册成功
          this.$message.success("注册成功，即将跳转至登录");

          //表单数据清空
          this.$refs.RegForm.resetFields()
          //切换到登录
          this.activeName = "first"
        }else {
          return false
        }
      })
    },

    //登录按钮
    LoginSubmit(){
      this.$refs.LoginForm.validate(async value => {
        if (value) {
          let {data} =await this.$axios({
            method:"POST",
            url:"/login",
            data: this.LoginForm
          })

          //登陆失败
          if (data.code){
            return this.$message.error(data.message)
          }

        //登陆成功
          this.$message.success("登陆成功")//弹窗
          this.$refs.LoginForm.resetFields()//清空登录状态的信息
          this.ifShow = false//关闭弹窗
          this.loginSuccess(data.data)//存到vuex

        }else {
          return false
        }
      })
    },

    ...mapMutations(['loginSuccess'])
  }
}
</script>

<style scoped lang="less">
//登录部分
.login{
  padding: 5px 0;

  .box{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);

    .main {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 300px;
      padding: 10px 20px;
      background-color: #fff;
    }
  }
}
</style>